@import "../style.module.scss";
$anime: 0.8s linear 1s infinite alternate blink;
@mixin assignAnimation($properties, $value) {
    @each $property in $properties {
        #{$property}: $value;
    }
}
@keyframes CloudFloat {
    from {
        transform:  translateY(0%);
        opacity: 0.7;
    }
    to {
        transform:  translateY(15%);
        opacity: 1;
    }
}
.KindBottom{
    position: absolute;
    bottom: 10vh;
}

.BCIcon {
    position: fixed;
    margin-top: 3vh;
    z-index: 2;
    width: 100vw;
    img {
        height: 5vw;
        width: auto;
        margin: 5vw;
        min-height: 25px;
    }
}
.maxHeight {
    height: 100vh;
}

