@import "../style.module.scss";

.maxHeight {
    height: 100vh;
}

.letterImg {
    max-width: inherit;
    padding: 10px 0px;
}

.letterDiv {
    // position: absolute;
    margin-left: 1em;
    margin-right: 1em;
    min-height: 600px;
    background-color: rgba(223, 223, 223, 0.746);
}
.shapeIcon {
    position: absolute;
    bottom: 0;
}
.clickPen {
    width: 7vw;
    height: auto;
    margin-bottom: 2vh;
    &:active {
        width: 10vw;
    }
}

.ReceiveMom {
    position: absolute;
    left: 9.5%;
    top: 36%;
    width: 18vw;

    img {
        width: inherit;
    }
}
.ReceiveDad {
    position: absolute;
    left: 31%;
    top: 36%;
    width: 18vw;
    img {
        width: inherit;
    }
}
.ReceiveGrandMa {
    position: absolute;
    right: 6%;
    top: 38%;
    width: 18vw;
    img {
        width: inherit;
    }
}
.ReceiveGrandPa {
    position: absolute;
    right: 29%;
    top: 38%;
    width: 18vw;
    img {
        width: inherit;
    }
}
.ReceiveType {
    position: absolute;
    left: 50%;
    bottom: 15%;
    width: 50vw;
    transform: translate(-50%, -50%);
    img {
        width: inherit;
    }
    
}
.textStyle {
    position: absolute;
    left: 50%;
    bottom: 18%;
